
import React, { useState, useEffect } from "react";
import "../styles.css";
import {API } from "../backend";
import Base from "./Base";
import Card from "./Card";
import { getProducts } from "./helper/coreapicalls";
import { loadCart } from "./helper/CartHelper";
import StripeCheckout from "./StripeCheckout";


const Cart = () =>{
    
   const [ products , setProducts] = useState([]);
   const [reload , setReload] = useState(false);

   useEffect( () =>{
       setProducts(loadCart());

   },[reload]);
   const loadAllProducts = () =>{
       return(
        <div>
           <h2> This section is loadAllProducts</h2>
           {products.map(( product , index ) =>(
            <Card 
            key={index}
            product={product}
            addtocart={false}
            removeFromCart={true}
            setReload = {setReload}
            reload={reload}

               /> 
           ))}
       </div>
       )   
   };

   const loadCheckout = () =>{
    return(
     <div>
        <h2> This section for loadCheckout</h2>
    </div>
    )    
}


    return(
        <Base title="Save your product here" description ="Ready to Buy">
           
           <div className="row "> 
         
           <div className="col-8">{loadAllProducts()}</div>
           <div className="col-4 ">

           <StripeCheckout
               products={products}
               setReload={setReload}
               
           />
           </div>

             </div>
          

        </Base>
          
       
    );
}
export default Cart;